*{
  background-color: #cbd5e1;
}

.main{
  display: flex;
  -webkit-box-pack: justify;
  gap: 2rem;
  justify-content: space-between;
  padding:2rem;
  width:100%
}

header{
    -webkit-box-pack: center;
    justify-content: center;
    font-family: "Zen Tokyo Zoo", cursive;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    margin: 0px auto;
    color: rgb(238, 238, 238);
    font-size: 2.2rem;
    background-color: #64748b;
    width:100%
}

.Resume {
  display:flex;
}


.Personal-Info{
  display:flex;
  justify-content: space-between;
  gap: 5rem;
}

label{
  display: flex;
  width: 250px;
  text-align: right;
}

.cv-view{
  min-width: 595px;
  border-radius: 2px;
  border: 1px solid #000;
  box-shadow: 5px 0 6px 0 hsl(0deg 0% 0% / 20%);
  justify-content: space-between;
  padding-right: 1rem;;

}

.full-name{
  color:#195c70
}

.current-job, .task-text{
  color:#5e5e5e
}

.education-title, .work-title, .Tasks{
  color: #81aebb
}
.education-content, .work-content{
  display:flex;
  justify-content: space-between;
}
.education-info{
  display:flex;
  gap:.2rem
}

.work-edit, .education-edit, .personal-edit{
  display:flex;
  flex-direction: column;
}
.company-time{
  display:flex;
  gap:.2rem
}

.date{
  display:flex;
}

.task-text{
  word-wrap: break-word
}

.cv-view{
  padding-left:2rem
}

